
import "./style/index.scss";
import { Vue, Options } from "vue-class-component";
@Options({
  name: "dialogPage"
})
export default class ExDialog extends Vue {
  dialogVisible1 = false;
  dialogVisible2 = false;
  dialogVisible3 = false;
  outerVisible = false;
  innerVisible = false;
  clickHandle(key: string) {
    this[key] = true;
  }
  get userList() {
    const list = [];
    for(let i = 0; i < 100; i++) {
      list.push('xx' + i);
    }
    return list;
  }
}
